import * as React from "react"
import type { HeadFC, PageProps } from "gatsby"

import Layout from "../../layout"
import Box from "../../components/Box"

import data from "../../data/teams"


import "./index.less"


const TeamPage: React.FC<PageProps> = () => {

  const teachers = data.teachers.map((v, i) => {

    const avatar = {
      background: "url(" + v.avatar + ") center center / cover no-repeat"
    }

    const website = v.website ?
      (<a href={v.website.href}>主页：{v.website.name} </a>) : (<></>)


    return (
      <div className="teacher-item">
        <div className="avatar" style={avatar} />
        <div className="name">{v.name}</div>
        <div className="title">{v.title}</div>
        <div className="institution">{v.institution}</div>
        <div className="researching">{v.researching}</div>
        <div className="website">
          {website}
        </div>
        <div className="email">
          <a href={"mailto:" + v.email}>邮箱：{v.email}</a>
        </div>
        <div className="location">办公地址：{v.location}</div>
      </div>
    )
  })

  const doctors = data.students.doctors.map((v, i) => {
    const avatar = v.avatar ? {
      background: "url(" + v.avatar + ") center center / cover no-repeat"
    } : {}
    const email = v.email ? (<a href={"mailto:" + v.email}>{v.email}</a>) : (<></>)
    const website = v.website ? (<a href={v.website.href}>{v.website.name}</a>) : (<></>)

    return (
      <div className="stu-item">
        <div className="avatar" style={avatar} />
        <div className="name"> {v.name} </div>
        <div className="email">
          {email}
        </div>
        <div className="location">{v.location}</div>
        <div className="website">
          {website}
        </div>
        <div className="researching">{v.researching}</div>
      </div>
    )
  })


  console.log(teachers)
  return (
    <Layout>
      <Box title="老师" className="teachers"> {teachers} </Box>
      <Box title="博士生" className="doctors"> { doctors } </Box>
    </Layout>
  )
}

export default TeamPage
export const Head: HeadFC = () => <title> XDU - ETVP - Team </title>